import React, { useState, useEffect } from 'react'
import { getDatabase, set, ref, get, child, update, push } from "firebase/database"
import { app } from '../firebaseConfig';

const db = getDatabase(app);

function Income(props) {
  const [message, setMessage] = useState('');
   const [uid, setUid]=useState("");
  const [income, setIncome] = useState({
    name: '',
    amount: 0,
    method: 'cash',
    date: new Date().toISOString().split('T')[0],
    transaction: 'credit'
  });

  useEffect(()=>{
              console.log("This is uid from  income", props.uid)
              setUid(props.uid);
          },[props])
  function handleChange(e) {
    setIncome(prev => ({
      ...prev,
      [e.target.name]: e.target.value,
    }))

  }

  function submitData() {
    console.log(income)
    //     fetch('http://localhost:3001/history',{
    //       method: 'POST',
    //       body: JSON.stringify(income),
    //       headers:{"content-type":"application/json; charset=UTF-8"}  
    //        }).then(res => res.json())

    //  .then((data) => {   
    //     console.log(data)        

    //  }).catch(console.log)

    const transRef = ref(db, `users/${uid}/transactions`);
    const newTransRef = push(transRef);
    const uniqueId = newTransRef.key;
    set(newTransRef, {
      ...income,
      id: uniqueId,
    });


    setMessage('Data submitted successfully!'); // Show success message
  
    // Clear the message after 3 seconds
    setTimeout(() => {
      setMessage('');
    }, 3000);

    setIncome({
      name: '',
      amount: 0,
      method: 'cash',
      date: new Date().toISOString().split('T')[0],
      transaction: 'credit'
    })

  }

  return (<>
    <div className="title">Add Income </div>
    <div className="income-add">

      <label htmlFor="">Name</label>
      <input type="text" name="name" value={income.name} onChange={handleChange}  />

      <label htmlFor="">Amount</label>
      <input type="number" name="amount" value={income.amount} onChange={handleChange} />

      <label htmlFor="payment-method">Payment Method</label>
      <select id="payment-method" name="method" value={income.method} onChange={handleChange}>
        <option value="cash">Cash</option>
        <option value="card">Card</option>
        <option value="online">Online</option>

      </select>

      <label htmlFor="">Date</label>
      <input type="date" name="date" value={income.date} onChange={handleChange} />


      <button className="submit-btn" onClick={submitData}>Submit </button>
      {message && <div className="message">{message}</div>}
    </div>
  </>
  )
}

export default Income