import { useEffect, useState } from "react";
import React from 'react'

function Topbar(props) {
   const [total, setTotal] = useState({
    income:0,
    expense:0,
    balance: 0  
    });

    useEffect(()=>{
      setTotal({
        ...total,
        income:props.data.income,
        expense: props.data.expense,
        balance: props.data.balance
      })
    },[props])
  

  // console.log(props)

  return (
    <>
    <div className='top-box'>Balance {total.balance}</div>
    <div className='top-box'>Income {total.income}</div>
    <div className='top-box'>Expense {total.expense}</div>
    </>
  )
}

export default Topbar