import React, { useEffect, useState } from 'react';
import { getDatabase, ref, onValue } from "firebase/database";
import { app } from '../firebaseConfig';

const db = getDatabase(app);

function Transactions(props) {
  const [data, setData] = useState(props.info);
  const [uid, setUid] = useState(props.uid);

  useEffect(() => {
    setUid(props.uid); // Update UID when props.uid changes
    listenForDataUpdates();
  }, [props]);

  function listenForDataUpdates() {
    const dbRef = ref(db, `users/${uid}/transactions`);
    onValue(dbRef, (snapshot) => {
      if (snapshot.exists()) {
        const fetchedData = snapshot.val(); // Get the object from the snapshot
        const dataArray = Object.values(fetchedData); // Convert object to array
       // console.log("Fetched data as array:", dataArray);
        setData(dataArray); // Update the state with the new data
      } else {
        //console.log("No data available");
        setData([]); // Clear the data if no transactions are available
      }
    }, (error) => {
      console.error("Error listening for data updates:", error);
    });
  }

  if (!data ) {
    return <div>Loading...</div>;
  }

  return (
    <div>
      <p style={{ padding: "1vmin 3vmin" }}>Recent Transactions</p>
      <ul>
        {data.slice(-8).map((item, index) => (
          <li key={index} style={{
            backgroundColor: item.transaction === 'credit' ? 'rgb(51, 168, 78)' : 'rgb(241, 65, 65)' ,
            color: 'white',
            padding: "3px 10px",
           
          }}>
            <div>{item.name}</div>
            <div
              // style={{
              //   backgroundColor: item.transaction === 'credit' ? 'green' : 'red',
              //   color: 'white',
              //   padding: "3px 10px",
              //   borderRadius: '10px',
              // }}
            >
               {item.amount}
            </div>
          </li>
        ))}
      </ul>
    </div>
  );
}

export default Transactions;