import React, { useEffect, useState } from 'react';
import { getDatabase, ref, get, child, set, push } from "firebase/database";
import { app } from '../firebaseConfig';

const db = getDatabase(app);

function Expense(props) {

   
    const [uid, setUid]=useState("");
    const [expense, setExpense] = useState({
        name: '',
        amount: 0,
        method: 'cash',
        category: 'food',
        date: new Date().toISOString().split('T')[0], // Set current date in YYYY-MM-DD format
        transaction: 'debit',
    });
    const [data, setData] = useState([]);
    const [message, setMessage] = useState(''); // State to manage the message

     useEffect(()=>{
           console.log("This is uid from expense", props.uid)
            setUid(props.uid);
        },[props])
    
    function handleChange(e) {
        setExpense((prev) => ({
            ...prev,
            [e.target.name]: e.target.value,
        }));
    }

    function getData() {
        const dbRef = ref(db);
        get(child(dbRef, "users/kb/transactions"))
            .then((snapshot) => {
                if (snapshot.exists()) {
                    console.log("Fetched data:", snapshot.val());
                    setData(Object.values(snapshot.val()));
                } else {
                    console.log("No data available");
                }
            })
            .catch((error) => {
                console.error("Error fetching data:", error);
            });
    }

    function submitData() {

        if (!expense.name.trim()) {        
            setMessage('Expense name is required!'); // Show error message
            setTimeout(() => {
                setMessage('');
            }, 3000); // Clear the message after 3 seconds
            return; // Stop the function if the name is empty
        }

        const transRef = ref(db, `users/${uid}/transactions`);
        const newTransRef = push(transRef); // Generate a unique key
        const uniqueId = newTransRef.key; // Get the unique key

        const expenseWithId = {
            ...expense,
            id: uniqueId, // Add the unique ID to the expense object
        };

        set(newTransRef, expenseWithId)
            .then(() => {
                console.log("Expense submitted:", expenseWithId);
                setMessage('Data submitted successfully!'); // Show success message
                getData(); // Refresh the data after submission

                // Clear the message after 3 seconds
                setTimeout(() => {
                    setMessage('');
                }, 3000);
            })
            .catch((error) => {
                console.error("Error submitting expense:", error);
                setMessage('Error submitting data. Please try again.'); // Show error message

                // Clear the message after 3 seconds
                setTimeout(() => {
                    setMessage('');
                }, 3000);
            });

            setExpense({
                name: '',
                amount: 0,
                method: 'cash',
                category: 'food',
                date: new Date(),
                transaction: 'debit',
            })

    }

    return (
        <div>
            <div className="title">Add Expense</div>
            <div className="income-add">

                <label htmlFor="">Expense</label>
                <input type="text" name="name" value={expense.name} onChange={handleChange} required />

                <label htmlFor="">Amount</label>
                <input type="number" name="amount" value={expense.amount} onChange={handleChange} />

                <label htmlFor="payment-method">Payment Method</label>
                <select id="payment-method" name="method" onChange={handleChange}>
                    <option value="cash">Cash</option>
                    <option value="card">Card</option>
                    <option value="online">Online</option>
                </select>

                <label htmlFor="category">Category</label>
                <select id="category" name="category" onChange={handleChange}>
                    <option value="food">Food</option>
                    <option value="travel">Travel</option>
                    <option value="entertainment">Entertainment</option>
                    <option value="shopping">Shopping</option>
                    <option value="other">Other</option>
                </select>

                <label htmlFor="">Date</label>
                <input type="date" name="date" value={expense.date} onChange={handleChange} />

                <button className="submit-btn" onClick={submitData}>
                    Submit
                </button>
                {message && <div className="message">{message}</div>}

            </div>

           
            
        </div>
    );
}

export default Expense;