import React, { useEffect, useState } from 'react';
import { Chart } from "react-google-charts";

export const options = {
  title: "Expense Distribution",
  pieHole: 0.5, // Makes it a donut chart
  is3D: false, // Set to true for a 3D effect
  legend: { position: "bottom" },
  pieSliceText: "label",
  backgroundColor: "transparent",
  titleTextStyle: {
    fontSize: 28, // Change font size of the title
    bold: true, // Make the title bold
    color: "#333", // Change the title color
  },
  legendTextStyle: {
    fontSize: 20, // Change font size of the legend
    color: "#555", // Change the legend color
  },
};

export default function Content2(props) {
  const [arrI, setArrI] = useState([]);
  const [data1, setData1] = useState([["Category", "Amount"]]); // Chart data for categories
  const [data2, setData2] = useState([["Category", "Amount"]]); // Chart data for balance and expense

  // Update `arrI` and chart data when `props.catData` or `props.data` changes
  useEffect(() => {
    if (props.catData) {
      setArrI(props.catData);
      const updatedData1 = [["Category", "Amount"], ...props.catData];
      setData1(updatedData1);
    }
  }, [props.catData]);

  useEffect(() => {
    if (props.data) {
      const updatedData2 = [
        ["Category", "Amount"],
        ["Balance", props.data.balance],
        ["Expense", props.data.expense],
      ];
      setData2(updatedData2);
    }
  }, [props.data]);

  return (
    <div
      style={{
        width: "100%",
        display: "flex",
        justifyContent: "center",
      }}
    >
      {/* Chart for category distribution */}
      <Chart
        chartType="PieChart"
        width="98%"
        height="400px"
        data={data1}
        options={options}
      />

      {/* Chart for balance vs expense */}
      {/* <Chart
        chartType="PieChart"
        width="98%"
        height="400px"
        data={data2}
        options={options}
      /> */}
    </div>
  );
}

