import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { createUserWithEmailAndPassword, signInWithEmailAndPassword } from 'firebase/auth';
import { auth } from '../firebaseConfig';
import { app } from '../firebaseConfig';
import { getDatabase, ref, get, child, set, push } from "firebase/database";


const db = getDatabase(app);


function Login() {
    const [email, setEmail] = useState(null);
    const [password, setPassword] = useState('');
    const [loginActive, setLoginActive] = useState(true)
    const [message, setMessage] = useState("")
    const navigate = useNavigate();
    const [uid, setUid]=useState("");
    

    function handleChange(e) {
        console.log(e.target.value);
        setEmail(e.target.value);
    }

    async function handleSubmit(e) {
        e.preventDefault(); // Prevent default form submission behavior
        console.log("submit ", email);


        if (loginActive) {

            // For Log IN
            signInWithEmailAndPassword(auth, email, password)
                .then((userCredential) => {
                    // Signed in
                    const user = userCredential.user;

                    console.log(user);
                    setMessage("Login Successful ");
                    setTimeout(() => {
                        setMessage('');
                    }, 3000);
                    navigate("/Dashboard")
                })
                .catch((error) => {
                    const errorCode = error.code;
                    const errorMessage = error.message;
                    console.log(error, errorCode, errorMessage);
                    setMessage(errorMessage);
                    setTimeout(() => {
                        setMessage('');
                    }, 3000);
                });


        } else {


            await createUserWithEmailAndPassword(auth, email, password)
                .then((userCredential) => {
                    // Signed in
                    const userCred = userCredential.user;
                    console.log(userCred);
                    setUid(userCred.uid)
                    setMessage("Sign in Successful ");
                    setTimeout(() => {
                        setMessage('');
                    }, 3000);
                    navigate("/")

 //DB CREATION
                    const userRef = ref(db, `users/${userCred.uid}/transactions`);
                    push(userRef,);
                    
                    // set(newPostRef, {
                    //     // ...
                    // });

                    // ...
                })
                .catch((error) => {
                    const errorCode = error.code;
                    const errorMessage = error.message;
                    console.log(error, errorCode, errorMessage);
                    setMessage(errorMessage);
                    setTimeout(() => {
                        setMessage('');
                    }, 3000);
                    // ..
                });
        }

        // navigate("/Dashboard")



    }

    return (
        <div className="loginContainer">
            <div className="login">
                {loginActive ? <div className="title">Login</div> : <div className="title">Sign in</div>}
                <label htmlFor="">Email</label>
                <input type="text" name="email" id="" onChange={handleChange} />
                <label htmlFor="">Password</label>
                <input type="password" name="password" id="password" value={password}
                    onChange={(e) => setPassword(e.target.value)}
                    required />

                {message && <div className='message'>{message}</div>}
                {/* <input type="submit" value="Login" className=""   /> */}
                <button onClick={handleSubmit} >Submit</button>

                {loginActive ? <button onClick={() => setLoginActive(!loginActive)}>Create account</button> :
                    <button onClick={() => setLoginActive(!loginActive)}>Login</button>}
            </div>

        </div>
    );
}

export default Login;