import React, { useEffect, useState } from 'react';
import boyImage from '../assets/boy.png';
import Modal from 'react-modal';
import Expense from './Expense';
import Income from './Income';
import {  signOut } from "firebase/auth";
import { auth } from '../firebaseConfig';
import { useNavigate } from 'react-router-dom';

// Set the app element for react-modal
Modal.setAppElement('#root');

function Sidebar(props) {
    const [expenseVisible, setExpenseVisible] = useState(false);
    const [incomeVisible, setIncomeVisible] = useState(false);
    const [uid, setUid]=useState("");
    const [username, setUsername]= useState("");
    
    const navigate = useNavigate();


    useEffect(()=>{
       // console.log("props uid ",props.data)
        setUid(props.data);
        setUsername(props.username)
    },[props])

    function handleLogout(){

        if(window.confirm("Do You want to sign out")){
        //console.log("Yes")

        signOut(auth).then(() => {
            // Sign-out successful.
                navigate("/");
                console.log("Signed out successfully")
            }).catch((error) => {
            // An error happened.
            console.log(error)
            });
        }
        else{
            console.log("NO !!!!!")

        }
    }
    return (
        <>
            <div id="sidebar">
                <img id="userImg" src={boyImage} alt="user" />
                <div>{username}</div>
                <div className="buttons">
                <button>Dashboard</button>
                <button onClick={() => setExpenseVisible(true)} >Expense</button>
                <button onClick={() => setIncomeVisible(true)} >Income</button>
                <button onClick={handleLogout}>Logout</button>
                </div>
                
        {/* Expense Modal */}
                <Modal 
                    className="modal"
                    overlayClassName="modal-overlay"
                    isOpen={expenseVisible}
                >
                    <button className='buttonX' onClick={() => setExpenseVisible(false)}>X</button>
                    <Expense uid={uid} />
                </Modal>


        {/* Income Modal */}
                <Modal
                    className="modal"
                    overlayClassName="modal-overlay"
                    isOpen={incomeVisible}>
                    <button className='buttonX' onClick={() => setIncomeVisible(false)}>X</button>
                        <Income uid={uid}/>
                </Modal>
            </div>
        </>
    );
}

export default Sidebar;