import React, { useEffect, useState } from 'react';
import Sidebar from './Sidebar';
import Topbar from './Topbar';
import Transactions from './Transactions';
import Content2 from './Content2';
import { getDatabase, ref, onValue } from "firebase/database";
import { app, auth } from '../firebaseConfig';
import { onAuthStateChanged } from "firebase/auth";

const db = getDatabase(app);

function Dashboard() {
  const [data, setData] = useState(null);
  const [uid, setUid] = useState("");
  const [username, setUsername]= useState("");

  const [categoryData, setCategoryData] = useState(null);
  const [total, setTotal] = useState({
    income: 0,
    expense: 0,
    balance: 0,
  });

  useEffect(() => {
    // Listen for authentication state changes
    onAuthStateChanged(auth, (user) => {
      if (user) {
        setUid(user.uid);
        // console.log("uid", user.uid, user.email.split("@")[0]);
        setUsername(user.email.split("@")[0]);
        
        listenForDatabaseUpdates(user.uid); // Start listening for database updates
      } else {
       // console.log("User is logged out");
      }
    });
  }, []);

  function listenForDatabaseUpdates(uid) {
    const dbRef = ref(db, `users/${uid}/transactions`);
    onValue(dbRef, (snapshot) => {
      if (snapshot.exists()) {
        const fetchedData = snapshot.val(); // Get the object from the snapshot
       // console.log("Fetched data", fetchedData);
        const data = Object.values(fetchedData); // Convert object to array
       // console.log("Fetched data as array:", data);
        setData(data); // Set the converted array to state

        // Calculate income, expense, and balance
        const income = data.reduce((sum, i) => {
          if (i.transaction === "credit") {
            sum += Number(i.amount);
          }
          return sum;
        }, 0);

        const expense = data.reduce((sum, i) => {
          if (i.transaction === "debit") {
            sum += Number(i.amount);
          }
          return sum;
        }, 0);

        setTotal({
          income,
          expense,
          balance: income - expense,
        });

        // Calculate category data
        const arrCategories = [
          ["food", 0],
          ["travel", 0],
          ["entertainment", 0],
          ["other", 0],
          ["shopping", 0],
        ];

        arrCategories.forEach((ar) => {
          ar[1] = data.reduce((sum, i) => {
            if (i.category === ar[0]) {
              sum += Number(i.amount);
            }
            return sum;
          }, 0);
        });

        setCategoryData(arrCategories);
      } else {
        console.log("No data available");
        setData([]);
        setTotal({
          income: 0,
          expense: 0,
          balance: 0,
        });
        setCategoryData(null);
      }
    }, (error) => {
      console.error("Error listening for database updates:", error);
    });
  }

  return (
    <div>
      {/* {uid} */}
      <div className="container">
        <div className="sidebar"><Sidebar data={uid} username={username}/></div>
        <div className="topbar"><Topbar data={total} /></div>
        <div className="content1"><Transactions info={data} uid={uid} /></div>
        <div className="content2"><Content2 data={total} catData={categoryData} /></div>
      </div>
    </div>
  );
}

export default Dashboard;